@use "./config/rest.scss";
@use "./config/variaveis.scss";

body {
    background-color: variaveis.$cor-de-fundo;
}

.nav-mobi {
    display: none;
}

section {
    padding-top: 2rem;
}
.container {
    margin: 0 auto;
    width: 100%;
    max-width: 1024px;
}

header .container {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    .logo {
        display: inline-flex;
        align-items: center;
    }
    .icone {
        display: inline-flex;
        align-items: center;
        gap: 5px;
    }

    img {
        max-width: 90px;
    }
}

header nav {
    background-color: variaveis.$cor-primaria;

    input {
        display: none;
    }
    ul {
        display: flex;
        list-style: none;
        padding: 1rem;
        gap: 3rem;
        justify-content: center;
        a {
            text-decoration: none;
            color: variaveis.$cor-branca;
            font-weight: bolder;
        }
    }
}

@media screen and (min-width: 320px) and (max-width: 1023px) {
    .nav-mobi {
        display: block;
        position: fixed;
        bottom: 0;
        left: 0s;
        width: 100%;
        background-color: variaveis.$cor-primaria;

        ul {
            text-align: center;
            display: flex;
            justify-content: space-around;
            list-style: none;

            a {
                text-decoration: none;
                font-weight: bold;
                color: variaveis.$cor-branca;
            }
        }
    }
    .hamburger-menu {
        #menu__toggle {
            opacity: 0;
        }
        #menu__toggle:checked + .menu__btn > span {
            transform: rotate(45deg);
        }
        #menu__toggle:checked + .menu__btn > span::before {
            top: 0;
            transform: rotate(0deg);
        }
        #menu__toggle:checked + .menu__btn > span::after {
            top: 0;
            transform: rotate(90deg);
        }
        #menu__toggle:checked ~ .menu__box {
            left: 0 !important;
        }

        .menu__btn {
            position: fixed;
            top: 20px;
            left: 20px;
            width: 26px;
            height: 26px;
            cursor: pointer;
            z-index: 1;
        }
        .menu__btn > span,
        .menu__btn > span::before,
        .menu__btn > span::after {
            display: block;
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: black;
            transition-duration: 0.25s;
        }
        .menu__btn > span::before {
            content: "";
            top: -8px;
        }
        .menu__btn > span::after {
            content: "";
            top: 8px;
        }
        .menu__box {
            display: block;
            position: fixed;
            top: 0;
            left: -100%;
            width: 300px;
            height: 100%;
            margin: 0;
            padding: 80px 0;
            list-style: none;
            background-color: variaveis.$cor-primaria;
            box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
            transition-duration: 0.25s;
        }
        .menu__item {
            display: block;
            padding: 12px 24px;
            font-size: 20px;
            font-weight: 600;
            text-decoration: none;
            transition-duration: 0.25s;
            border-bottom: 1px solid rgba(0, 0, 0, 0.138);
        }
        .menu__item:hover {
            background-color: #cfd8dc;
        }
    }

    header .container {
        justify-content: center;
        .icone {
            display: none;
        }
        .logo {
            h1 {
                font-size: 23px;
            }
            img {
                max-width: 60px;
                gap: 30px;
            }
        }
    }
    footer {
        display: none;
    }
}
